import { Component, OnInit,ViewChild } from '@angular/core';
import { NavController ,Platform} from '@ionic/angular';
import { SpeechRecognition } from '@ionic-native/speech-recognition/ngx';
import { ChangeDetectorRef } from '@angular/core';
import { Media, MediaObject } from '@ionic-native/media/ngx';
import { Storage } from '@ionic/storage';
import { File } from '@ionic-native/file/ngx';

const MEDIA_FILES_KEY = 'mediaFiles';



@Component({
  selector: 'app-speech',
  templateUrl: './speech.page.html',
  styleUrls: ['./speech.page.scss'],
})
export class SpeechPage implements OnInit {

  // Declares the required variables before the constructor.
  mediaFiles = [];
  @ViewChild('myvideo') myVideo: any;
  public matches: String[]=['Result of speech Recording is showed here'];
  isRecording = false;

  //Inject to the constructor.
 constructor(
   public navCtrl: NavController, 
   private speechRecognition: SpeechRecognition, 
   public platform: Platform,
   private cd: ChangeDetectorRef,
    private media: Media,
   private storage:Storage,
   private file:File
   ) { }

  // isIos() {
  //   return this.platform.is('ios');
  // }

  // function for start recording audio on smartphone
  // Call it from `ionViewDidLoad`.

     ionViewDidLoad() {
      this.storage.get(MEDIA_FILES_KEY).then(res => {
        this.mediaFiles = JSON.parse(res) || [];
      });
    }
   stopListening() {
    this.speechRecognition.stopListening().then(() => {
      this.isRecording = false;
    });
  }
  //get permission for speechrecognition
  getPermission() {
    this.speechRecognition.hasPermission()
      .then((hasPermission: boolean) => {
        if (!hasPermission) {
          this.speechRecognition.requestPermission();
        }
      });
  }

  startListening(){
  	this.matches=[];
    let options = {
      language: 'en-US'
    }
    this.speechRecognition.startListening(options).subscribe(matches => {
      this.matches = matches;
      this.cd.detectChanges();
    });
    this.isRecording = true;
  }
 
  // function for play audio/sound file.
  playRecordingfile(myFile) {
      this.matches=[];
        if (myFile.name.indexOf('.wav') > -1) {
          const audioFile: MediaObject = this.media.create(myFile.localURL);
          audioFile.play();
          this.speechRecognition.startListening().subscribe(matches => {
            myFile=this.matches;
            this.matches = matches;
            this.cd.detectChanges();
          });
        }
        myFile.onError.subscribe(error => console.log('Error!', error));
  }
  //store media file
  storeMediaFiles(files) {
    this.storage.get(MEDIA_FILES_KEY).then(res => {
      if (res) {
        let arr = JSON.parse(res);
        arr = arr.concat(files);
        this.storage.set(MEDIA_FILES_KEY, JSON.stringify(arr));
      } else {
        this.storage.set(MEDIA_FILES_KEY, JSON.stringify(files))
      }
      this.mediaFiles = this.mediaFiles.concat(files);
    })
  }

  ngOnInit() { 
   
  }
  

}
